.arrows {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  &__prev,
  &__next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2; }
  .link__text {
    @include raj-m;
    @include sm {
      display: none; } }
  .link__pic {
    .icon {
      width: 8px;
      height: 22px; } }
  &__prev {
    left: 20px;
    @include sm {
      left: 2px; }
    .link__text {
      margin: 0 0 0 6px; }
    .icon {
      @include rotate(-180); } }
  &__next {
    right: 20px;
    @include sm {
      right: 1px; }
    .link__text {
      margin-right: 6px; } } }
