.desc {
  max-width: 445px;
  margin: 40px auto 100px auto;
  @include sm {
    margin: 20px auto 40px auto; }
  &__head {
    padding: 23px 25px 12px;
    background: $red;
    color: $white;
    @include clr; }
  &__pic {
    float: left;
    margin-right: 17px; }
  &__caption {
    overflow: hidden;
    font-size: 20px;
    color: $white;
    padding: 3px 0;
    @include raj-sb; }
  &__content {
    background: $gray-light2;
    padding: 19px 32px 25px 28px;
    box-shadow: 0px 2px 4px 0 rgba(0,0,0, .15); }
  &__line {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 6px; }
  &__title {
    @include raj-m; }
  &__text {
    p {
      position: relative;
      font-size: 12px;
      line-height: 1.2;
      @include raj-m;
      &:not(:last-child) {
        margin-bottom: 16px; } }
    .js-shave-char {
      cursor: pointer;
      @include raj-sb; } } }
