@mixin raj-l {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 300; }
@mixin raj-r {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 400; }
@mixin raj-m {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500; }
@mixin raj-sb {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 600; }
@mixin raj-b {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 700; }


html,
body {
  height: 100%; }

body {
  position: relative;
  background: $bg;
  line-height: 1.4;
  font-size: 16px;
  color: $black;
  min-width: 320px;
  @include raj-r; }

a {
  text-decoration: none; }

button {
  background: transparent; }

img {
  max-width: 100%; }

.link {
  font-size: 22px;
  color: $black;
  white-space: nowrap;
  @include tr(opacity .3s);
  &:hover {
    opacity: .8; }
  @include md {
    font-size: 18px; }
  &__text {
    margin-right: 20px;
    line-height: 1; }
  &__pic {
    line-height: .5; }
  > span {
    display: inline-block;
    vertical-align: middle; }
  .icon {
    width: 5px;
    height: 20px;
    stroke: $black; } }
